.wrap {
    display: flex;
    justify-content: center;
    background-color: var(--backgoud-main);
    padding-top: 30px;
    min-width: 100%;
    @keyframes moveOto {
        0% {
            transform: translateX(180%);
            opacity: 0;
        }
        50% {
            opacity: 1;
        }
        100% {
            transform: translateX(-420%);
            opacity: 0;
        }
    }
    @-webkit-keyframes moveOto {
        0% {
            transform: translateX(180%);
            -webkit-transform: translateX(180%);
            opacity: 0;
        }
        50% {
            opacity: 1;
        }
        100% {
            transform: translateX(-420%);
            -webkit-transform: translateX(-420%);
            opacity: 0;
        }
    }
    .content {
        display: flex;
        flex-direction: column;
        padding-top: 40px;
        width: 80vw;
        .banner {
            display: flex;
            justify-content: space-between;
            width: 100%;
            height: 50vh;
            border-radius: 20px;
            background: var(--banner-color);
            overflow: hidden;
            img:first-child {
                margin-left: 30px;
                object-fit: cover;
                height: 150%;
                animation: moveImage 1.5s infinite alternate;
                z-index: 1;
                @keyframes moveImage {
                    0% {
                        transform: rotate(4deg);
                    }
                    100% {
                        transform: rotate(-6deg);
                    }
                }
            }

            img:nth-child(2) {
                align-self: flex-end;
                height: 30%;
                animation: moveOto 5s ease-in-out infinite;
                -webkit-animation: moveOto 5s ease-in-out infinite;
            }

            img:last-child {
                height: 50%;
                align-self: flex-end;
            }
        }
        .video-lessons,
        .text-lessons {
            min-height: 40vh;
            margin-top: 40px;

            h2 {
                margin-bottom: 20px;
                font-size: 24px;
                font-weight: 700;
                line-height: 34px;
            }
            .wrap {
                display: flex;
                justify-content: space-around;
                flex-wrap: wrap;
                padding: 20px 0;
                border-radius: 20px;
                background-color: var(--backgoud-primary);
                box-shadow: var(--box-shadow);
                .lessons {
                    display: flex;
                    width: 413px;
                    min-height: 15vh;
                    margin: 10px 0;
                    padding: 20px 26px 20px 20px;
                    border-radius: 16px;
                    background-color: var(--primari-color);
                    &:hover {
                        cursor: pointer;
                        opacity: 0.8;
                    }
                    img {
                        border-radius: 10px;
                        width: 116px;
                        height: 116px;
                    }
                    .title {
                        margin-left: 20px;
                        h3 {
                            font-size: 16px;
                            font-weight: 700;
                            line-height: 24px;
                        }
                        p {
                            margin-top: 10px;
                            font-size: 16px;
                            font-weight: 400;
                            line-height: 24px;
                        }
                    }
                }
            }
        }
        .text-lessons {
            margin-top: 40px;
        }
    }
}

@media (min-width: 740px) and (max-width: 1023px) {
    .wrap {
        max-width: 100%;
        .content {
            .banner {
                height: 30vh;
            }
        }
    }
}

@media (min-width: 0px) and (max-width: 739px) {
    .wrap {
        max-width: 100%;
        .content {
            padding-top: 20px;
            height: 100%;
            width: 100%;
            .banner {
                height: 20vh;
            }
            .video-lessons,
            .text-lessons {
                margin-top: 20px;
                h2 {
                    font-size: 20px;
                    margin-bottom: 10px;
                }
                .wrap {
                    margin: 0;
                    padding: 0;
                    height: 464px;
                    overflow-x: auto;
                    .lessons {
                        width: 80%;
                    }
                }
            }
        }
    }
}
